import React, { Component } from "react";
import { Paper, Card, Typography, Grid, CardContent } from "@material-ui/core";
import { connect } from "react-redux";
import { withStyles } from "@material-ui/core/styles";
import "./style.css";
import DataProvider from "../../LocalDB";
import ShopProvider from "../../LocalDB/ShopDB";
import CIcon from "@coreui/icons-react";
import Controls from "../../components/controls/Controls";
import feature from "./data";

const style = (theme) => ({
  main: (props) => {
    return {
      ...theme.GlobalBox,
      overflow: "hidden",
    };
  },
  Body: {
    ...theme.GlobalBox,
    background: theme.palette.light.main,
    padding: 8,
    width: "100%",
  },
  info: {
    ...theme.GlobalBox,
    background: theme.palette.light.main,
    height: "220px",
  },
  activate: {
    ...theme.GlobalBox,
    background: theme.palette.light.main,
    height: "100%",
    // border: `1px solid ${theme.palette.primary.main} `,
    // justifyContent: "center",
    // alignItems: "center",
  },
  Status: {
    ...theme.GlobalBox,
    height: "100px",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "40%",
    flexShrink: 0,
    alignItems: "center",
    borderBottom: "1px solid #f0f0f0",
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
    alignItems: "center",
    borderBottom: "1px solid #f0f0f0",
  },
  Title: {
    textAlign: "center",
  },
  Content: {
    textAlign: "center",
  },
  avatar: {
    backgroundColor: "#00000000",
    width: 200,
  },
  feature: {
    borderRadius: 25,
    border: `1px solid ${theme.palette.primary.main} `,
    height:260
  },
});

class GrowPos extends Component {
  constructor(props) {
    super(props);
    this.updateDimensions = this.updateDimensions.bind(this);
    this.handleTabChange = this.handleTabChange.bind(this);
    this.state = {
      width: 0,
      height: 0,
      activeIndex: 0,
      current: null,
    };
  }
  handleTabChange(activeIndex, current) {
    this.setState({ activeIndex, current });
  }
  render() {
    const { height } = this.state;
    const { classes } = this.props;
    return (
      <Paper
        className={classes.Body}
        style={{ height: `${height}px`, maxHeight: `${height}px` }}
      >
        <ShopProvider>
          <DataProvider>
            <Grid container spacing={1}>
              <Grid
                item
                xs={12}
                sm={6}
                md={6}
                className={classes.main}
                direction="row"
                style={{ height: `${height - 16}px` }}
              >
                <Paper className={classes.activate} variant="outlined">
                  <div className={classes.Title}>
                    <img
                      src={"http://localhost:4545/static/img/icon.png"}
                      className={classes.avatar}
                      alt="icon"
                    />
                  </div>
                  <div className={classes.Content} style={{ padding: 24 }}>
                    <Typography variant="h5">
                      Open source Solutions For Your Business
                    </Typography>
                    <Typography variant="body2" display="inline">
                      Restaurant, Cafe, QSR and more to manage your business
                      like a PRO. Billing and Restaurant Point of Sale software
                      and more to simplify and easy operations and maximize
                      profits.
                    </Typography>
                  </div>
                  <div className={classes.Content} style={{ padding: 24 }}>
                    <Typography variant="caption">www.growpos.in</Typography>
                    <div>
                      <CIcon
                        name={"cibFacebook"}
                        height={20}
                        style={{ margin: 8 }}
                      />
                      <CIcon
                        name={"cibGithub"}
                        height={20}
                        style={{ margin: 8 }}
                      />
                    </div>
                  </div>
                </Paper>
              </Grid>
              <Grid
                item
                xs={12}
                sm={6}
                md={6}
                className={classes.main}
                direction="row"
                style={{ height: `${height - 16}px` }}
              >
                <Paper className={classes.activate} variant="outlined">
                  <Grid container spacing={1}>
                    {feature.map((item) => (
                      <Grid item xs={12} sm={6} md={6} id={item._id}>
                        <Card className={classes.feature}>
                          <CardContent>
                            <div className={classes.Title}>
                              <CIcon name={item.icon} height={70} />
                            </div>
                            <div className={classes.Content}>
                              <Typography variant="h6">
                                {item.heading}
                              </Typography>
                              <Typography variant="body2" display="inline">
                                {item.details}
                                Make your own delivery platform for free
                              </Typography>
                            </div>
                            <div className={classes.Content}>
                              <Controls.Button text={item.Button} />
                            </div>
                          </CardContent>
                        </Card>
                      </Grid>
                    ))}
                  </Grid>
                </Paper>
              </Grid>
            </Grid>
          </DataProvider>
        </ShopProvider>
      </Paper>
    );
  }

  updateDimensions = () => {
    this.setState({
      width: document.getElementById("Content").clientWidth,
      height: document.getElementById("Content").clientHeight,
    });
  };
  componentDidMount() {
    if (this.state.width !== document.getElementById("Content").clientWidth) {
      this.updateDimensions();
    }
    window.addEventListener("resize", this.updateDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }
}
const mapStateToProps = (state) => {
  return {};
};

export default connect(mapStateToProps)(
  withStyles(style, { withTheme: true })(GrowPos)
);
